import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';

import {Section, Container} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import step1 from '@images/art/step01.jpg';
import step2 from '@images/art/step02.gif';
import step3 from '@images/art/step03.gif';

const STEPS = [
  {
    header: 'Connect',
    text: 'Connect your Hai device to any of your Bluetooth audio sources (e.g. mobile phone).',
  },
  {
    header: ' Turning on T-mode',
    text: "Turning on the T-mode of your aids. Hai's DBC technology clears the buzzing sound while using the t-mode.",
  },
  {
    header: 'Enjoy and be fashionable',
    text: "Hai's neckcoil sends a high quality audio signal to your hearing aids and/or cochlear implants. All while looking good!",
  },
];

const Manual = () => (
  <StaticQuery
    query={graphql`
      query {
        Modularity: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "modularity_s" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobile: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "step01" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="usage" accent="primary">
        <a name="usage"></a>
        <SectionHeader 
          header="How to use" 
          subheader=""
          bg="inverse"
        />
        <Container>
          <Wrapper>
              <Grid>
                <div>
                      <Img fluid={data.Mobile.childImageSharp.fluid} />
                      <h3><i>1.</i> {STEPS[0].header}</h3>
                      <p>{STEPS[0].text}</p>
                      
                </div>
                <div>
                      <img  src={step2} alt="step2"/>
                      <h3><i>2.</i> {STEPS[1].header}</h3>
                      <p>{STEPS[1].text}</p>
                </div>
                <div>
                      <img  src={step3} alt="step3"/>
                      <h3><i>3.</i> {STEPS[2].header}</h3>
                      <p>{STEPS[2].text}</p>
                </div>
                
              </Grid>
              <GridNote>
                <div>
                  <Img fluid={data.Modularity.childImageSharp.fluid} />
                </div>
                <div> 
                  <h3><small>* Switch made easy</small></h3>
                  <p><small>You can attach one Hai earbud for your hearing side, and use telecoil for the side with hearing aid. Just choose what you need and modify the sound with our app.</small></p>
                </div>
              </GridNote>
          </Wrapper>
        </Container>
      </Section>
    )}
  />
);



const Wrapper = styled.div`
  padding: 60px 0 40px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S100};
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 0 0 60px 0;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    padding: 0 0 40px 0;
  }
  @media (max-width: ${props => props.theme.screen.xs}) {
    padding: 0 0 0 0;
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  grid-gap: 25px;
  text-align: left;
  align-items: top;
  justify-items: left;
  padding-bottom: 15px;
  img{
    width: 100%;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;


const GridNote = styled.div`
  display: grid;
  grid-template-columns: 1fr 7fr;
  grid-gap: 20px;
  text-align: left;
  align-items: top;
  justify-items: left;
  padding-bottom: 15px;
  border-top: 1px solid  ${props => props.theme.color.supplementary.S700};
  padding-top: 35px;
  margin-top: 10px;
  div{
    width: 100%;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
    display: none;
  }
`;


export default Manual;
