import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import Popup from "reactjs-popup";
import Video from '@ui/video';

import { Container } from '@components/global';
import arrow from '@images/art/arrow.svg';
import play from '@images/art/play.svg';
import EmailForm from '@ui/emailform';
//import Call2Act from '@ui/Call2Act';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        masthead: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "masthead_james_2" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1400) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        imgstack: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "allblack" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1400) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Grid>
          <div/>
          <Img fluid={data.masthead.childImageSharp.fluid}/>
        </Grid>
        <Container>
          <TextGrid>
            <Text>
              <Fade>
                <Title>
                <h1>Meet Hai,</h1>
                <h2>the most accessible music mate for the hard-of-hearing </h2>
                <p><br/><b><span role="img" aria-label="star">🌟</span>Sign up to get up-to-30% OFF on Hai when we launch!<span role="img" aria-label="star">🌟</span></b><br/></p>
                </Title>
                
                <EmailForm accent="inverse"/><br/>
                <ButtonPos>
                  <Fade>
                    <AnchorLink href="#intro">
                      <Call2Button>
                        <img src={arrow}
                          alt="button to the following intro"
                        />
                      </Call2Button>
                    </AnchorLink>
                  </Fade>
                </ButtonPos>
              </Fade>
            </Text>
            <Play>
              <StyledPopup trigger={<PlayButton><img src={play}
                          alt="button to the following intro"
                        /></PlayButton>} modal closeOnDocumentClick>
                <Video videoSrcURL="https://www.youtube.com/embed/_Ltk-tX5Org" videoTitle="main video"/>
              </StyledPopup>
            </Play>
          </TextGrid>
        </Container>
        
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.background.S400};
  padding-top: 50px;
  max-height: 1500px;
  overflow: hidden;
  height: 600px;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding-top: 0;
    height: 1020px;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    padding-top: 0;
    height: 850px;
  }
`;

const Grid = styled.div`
  position: relative;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 560px;
  overflow: hidden;
  display: grid;
  grid-template-columns: 4fr 4fr;
  align-items: center;
  margin: 0 auto;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    height: 450px;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    top: 0vh;
    height: 350px;
  }
`;

const TextGrid = styled.div`
  padding-top: 20px;
  position: relative;
  top: -550px;
  display: grid;
  grid-template-columns: 4fr 5fr;
  align-items: center;
  justify-items: center;
  justify-self: center;
  color: ${props => props.theme.color.grayscale.S100};

    @media (min-width: ${props => props.theme.screen.xs}) {
      max-width: 540px;
    }

    @media (min-width: ${props => props.theme.screen.sm}) {
      max-width: 720px;
    }

    @media (min-width: ${props => props.theme.screen.md}) {
      max-width: 960px;
      justify-self: start;
    }

    @media (min-width: ${props => props.theme.screen.lg}) {
      max-width: 1200px;
    }

    @media (min-width: ${props => props.theme.screen.xl}) {
      max-width: 2000px;
    }
    @media (max-width: ${props => props.theme.screen.xl}) {
      margin-top: 5vh;
    }
    @media (max-width: ${props => props.theme.screen.lg}) {
      margin-top: 0vh;
    }
    @media (max-width: ${props => props.theme.screen.md}) {
      display: ${props => props.disMd}; 
      grid-template-columns: 1fr;
      position: relative;
      top: -225px;
      padding-top: 0px;
      padding-bottom: 0px;
      grid-gap: 0px;
      order: 2;
    }
    @media (max-width: ${props => props.theme.screen.sm}) {
      top: -195px;
    }
`;

const Text= styled.div`
  @media (max-width: ${props => props.theme.screen.lg}) {
    margin-top: 15px;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    order: 2;
    margin-top:80px;
    margin-bottom: 0px;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    margin-top:30px;
  }

`

const ButtonPos = styled.div`
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  animation: MoveUpDown 0.9s linear infinite;
  z-index: 100;

  @keyframes MoveUpDown {
    0%, 100% {
      top: 18px;
    }
    50% {
      top: 33px;
    }
  }
`

const Call2Button = styled.div`
  display: inline-block;
  width: 30px;
  padding: 0 2px;
  vertical-align: middle;
  color:
  ${
    props => 
    props.accent === 'inverse'
      ? props.theme.color.grayscale.S100
      : props.theme.color.background.S100
  };  
  opacity: 0.7;
  -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  &:hover {
    cursor: pointer;
    opacity: 1;
  }
`

const Play = styled.div`
  align-items: center;
  justify-self: center;
  @media (min-width: ${props => props.theme.screen.sm}) {
    margin-left: calc(360px - 50%);
  }
  @media (min-width: ${props => props.theme.screen.md}) {
    margin-left: calc(480px - 50%);
  }
  @media (min-width: ${props => props.theme.screen.lg}) {
    margin-left: calc(600px - 50%);
  }
  @media (min-width: ${props => props.theme.screen.xl}) {
    margin-left: calc(1000px - 50%);
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    margin-bottom: 100px;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    margin-bottom: 100px;
  }
`

const PlayButton = styled.div`
  display: inline-block;
  width: 75px;
  padding: 0 2px;
  vertical-align: middle;
  background: ${props => props.theme.color.grayscale.S100}; 
  border-radius: 100px;
  border: none;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
  transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;

  
  &:hover {
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    background: ${props => props.theme.color.secondary.S100}; 
    cursor: pointer;
  }
`
const StyledPopup = styled(Popup)`
  &-content {
  background-color: transparent !important;
  border: none !important;
  width: 70% !important;
  @media (max-width: ${props => props.theme.screen.md}) {
    width: 100% !important;
  }
 }
  
`

const Title = styled.div`
  h1{
    padding: 0;
  }
  h2{
    padding: 0;
    margin-block-start: 0em;
    margin-block-end: 0em;
  }
 }
  
`

export default Header;
