import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import ImgSingle from '@ui/img_single';


const Intro = () => (
  <StaticQuery
    query={graphql`
      query {
        Sound: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "sound" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Allcolour: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "allcolour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Flexibility: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "flexibility" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "parkour" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro">
        <a name="intro"></a>
        <SectionHeader 
          header="Earphone for all" 
          subheader="We're launching a campaign to create the first high-end stereo earphone designed for you — whether you are a user of hearing aids, cochlear implants, or none of them."
          toggle2Act="true"
          text="Our Indiegogo pre-launch"
          href="https://www.indiegogo.com/projects/hai-world-s-first-bluetooth-earphones-for-all/coming_soon/x/23571765"
          target="_blank"
        />
        <ImgSingle
          header="Universal compatibility and connectivity" 
          text="Hai is compatible with all hearing aids and cochlear implants with T-mode. It can receive audio from all the Bluetooth sources and then send it to your devices."   
          togglecover="true"  
          toggletop="false"     
        >
          <Img fluid={data.Allcolour.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt 
          header="Born for Flexible use" 
          text="Hai lets you switch between earbuds and neckcoils to match the aids and ears. It beats most of the streamers failing to provide stereo sound to single-sided users or users of two different brands." 
        >
          <Img fluid={data.Flexibility.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Experience superior sound quality" 
          text="Unlike most of the streamers do not allow their users to enjoy stereo sound if they use single-sided aids or the aids by the different brands. We developed telecoil, an established and versatile technology, further to provide outstanding sound quality for the diverse aid usage scenarios."
        >
          <Img fluid={data.Sound.childImageSharp.fluid} />
        </ImgTxtInverse>
           
      </Section>
    )}
  />
);

export default Intro;
